@import '../../utils/styles/flex';
@import '../../utils/styles/theme';

.main {
    @include flex(column);
    width: 100%;


    .header {
        @include flex(row, flex-start, space-between);
        width: 100%;
        
        .logo {
            margin: $space-m $space-l;
        }
        
        .button {
            margin: $space-m;
            // margin-left: 150px;
            background-color: $color-grey;
        }
        
        .button:hover{
            background-color: #ededed4f;
        }
        .info {
            @include flex(row);
            margin: $space-m;



            .points {
                @include flex(row);
                margin: 0 $space-m 0 $space-m;
                padding: 0 $space-m;
                border-radius: 30px;
                background-color: $color-grey;
            }

        }
    }

    .background {
        width: 100%;
        height: 350px;
    }
    .title {
        color:white;
        font-size: 50px;
        transform: translate(-200%, 150%);
        position: absolute;
    }
}



